* {
  margin: 0;
  padding: 0;
}

.wrapp {
  justify-content: center;
  align-items: center;
  height: 60vh;
}

.line {
  width: 100vw;
}

.left, .right {
  width: 50vw;
  display: inline-block;
  overflow: hidden;
}

.left {
  color: pink;
  transform: skew(0deg, -15deg);
}

.right {
  color: #000;
  transform: skew(0deg, 15deg);
}

.left .content {
  text-align: center;
  width: 100vw;
}

.right .content {
  text-align: center;
  width: 100vw;
  transform: translate(-50vw);
}

.spanSlow, .spanFast {
  text-transform: uppercase;
  font-family: Montserrat, sans-serif;
  font-size: 10vw;
  font-weight: 900;
  line-height: .8;
  transition: all .6s ease-out;
  display: inline-block;
}
